<template>
  <div class="search">
    <template v-for="(item, index) in shopList">
      <Shop :key="index" :shop="item" @onShopClick="linkDetail" />
    </template>
    <span v-if="!shopList.length">未找到相关商品...</span>
  </div>
</template>

<script>
import { search } from "../../api/search.js";
export default {
  name: "Search",
  data() {
    return {
      shopList: [],
    };
  },
  methods: {
    getSearchShopList() {
      let word = this.$route.query.keyword;
      search(word).then((data) => {
        this.shopList = data;
      });
    },
    linkDetail(item) {
      this.$router.push(`/detail/${item.Id}?type_one=${item.type_one}`);
    },
  },
  created() {
    this.getSearchShopList();
  },
  watch: {
    // 监听路由地址变化
    $route: {
      deep: true,
      handler(to, from) {
        this.getSearchShopList();
      },
    },
  },
};
</script>

<style lang='less' scoped>
.search {
  padding: 50px;
  span {
    color: #333;
    font-size: 36px;
  }
}
</style>